//scss-docs-startspacer-variables-maps
$spacer:1rem!default;
$spacers:(
0:0,
1:$spacer*.25,
2:$spacer*.5,
3:$spacer,
4:$spacer*1.5,
5:$spacer*3,
)!default;

$negative-spacers:if($enable-negative-margins,negativify-map($spacers),null)!default;
//scss-docs-endspacer-variables-maps

//Position
