$body-bg:$white!default;
$body-color:$gray-900!default;
$body-text-align:null!default;

//Utilitiesmaps
//
//Extendsthedefault`$theme-colors`mapstohelpcreateourutilities.

//scss-docs-startutilities-colors
$utilities-colors:map-merge(
$theme-colors-rgb,
(
"black":to-rgb($black),
"white":to-rgb($white),
"body":to-rgb($body-color)
)
)!default;
//scss-docs-endutilities-colors

//scss-docs-startutilities-text-colors
$utilities-text-colors:map-loop($utilities-colors,rgba-css-var,"$key","text")!default;
//scss-docs-endutilities-text-colors

//scss-docs-startutilities-bg-colors
$utilities-bg-colors:map-loop($utilities-colors,rgba-css-var,"$key","bg")!default;
//scss-docs-endutilities-bg-colors

//Links
//
//Styleanchorelements.

$link-color:$primary!default;
$link-decoration:underline!default;
$link-shade-percentage:20%!default;
$link-hover-color:shift-color($link-color,$link-shade-percentage)!default;
$link-hover-decoration:null!default;

$stretched-link-pseudo-element:after!default;
$stretched-link-z-index:1!default;

//Paragraphs
//
