//adaptingtodifferentscreensizes,foruseinmediaqueries.

//scss-docs-startgrid-breakpoints
$grid-breakpoints:(
xs:0,
sm:576px,
md:768px,
lg:992px,
xl:1200px,
xxl:1400px
)!default;
//scss-docs-endgrid-breakpoints

@include_assert-ascending($grid-breakpoints,"$grid-breakpoints");
@include_assert-starts-at-zero($grid-breakpoints,"$grid-breakpoints");


//Gridcontainers
//
//Definethemaximumwidthof`.container`fordifferentscreensizes.

